<script>
import {
  BRow,
  BDropdown,
  BCol,
  BDropdownItem,
  BCard,
  BCardText,
  BCardTitle,
  BMedia,
  BMediaAside,
  BMediaBody,
  BImg,
  BCardBody,
  BLink,
  BPagination,
  BButton,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import ContentWithSidebar from '@core/layouts/components/content-with-sidebar/ContentWithSidebar.vue'
import moment from 'moment'
import Image from '@/assets/images/default.png'

export default {
  components: {
    BRow,
    BButton,
    BCol,
    BCard,
    BCardText,
    BCardBody,
    BCardTitle,
    BMedia,
    BMediaAside,
    BMediaBody,
    BLink,
    BDropdownItem,
    BDropdown,
    BImg,
    BPagination,
    ContentWithSidebar,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      logoUrl: 'https://dev.api.the-netwerk.com/storage/app/',
      Image,
      moment,
      search_query: '',
      currentPage: 1,
      perPage: 5,
      data: [],
    }
  },
  computed: {
    rows() {
      return this.data.length
    },
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      this.$http.get('blog').then(response => {
        this.data = response.data.data
      })
    },
    confirmText(id) {
      this.$swal({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          this.$http.delete(`blog/${id}`).then(response => {
            this.$swal({
              icon: 'success',
              title: 'Deleted!',
              text: response.data.message,
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
            this.getData()
          })
        }
      })
    },
    routetoBlog(id) {
      this.$router.push({
        name: 'blog-detail',
        params: { id },
      })
    },
  },
}
</script>

